/* Content */
.conatiner { padding: 15px }

.col {
    width: 23%;
    height: 210px;
    float: left;
    margin: 1%;
    @media #{$mediaExSmall} {
        width: 100%;
        margin: 2% 1%
    }
    @media #{$mediaSmall} {
        width: 48%;
        margin: 2% 1%
    }
    @media #{$mediaMedium} {
        width: 48%;
        margin: 2% 1%
    }
    @media #{$mediaLarge} {
        width: 31%;
        margin: 1%
    }
}

.content {
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 10px;
    box-shadow: 0 0 5px 0 #00000050;
    overflow: hidden;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        background-color: rgba(0, 0, 0, .45);
        z-index: 1;
    }
    .img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
        img {
            width: 100%;
            height: 100%;
            border-radius: 10px;
            transition: .3s ease;
        }
    }
    &:hover {
        img {
            transform: scale(1.1)
        }
    }
    .info {
        font-family: 'Satisfy', cursive;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 99;
        .btn {
            background-color: transparent;
            border: none;
            &:focus {
                outline: 0
            }
        }
        > .btn {
            position: absolute;
            top: 20px;
            left: 15px;
            font-size: 17px;
            color: #fff;
            cursor: pointer }
        .text {
            width: 100%;
            height: 100%;
            h3 {
                position: absolute;
                bottom: 30px;
                left: 15px;
                font-size: 30px;
                color: #fff
            }
            > .btn {
                position: absolute;
                bottom: 20px;
                right: 15px;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                font-size: 18px;
                background-color: #ff393b;
                border-radius: 50%;
                color: #fff;
                cursor: pointer;
                transition: .2s ease;
                &:hover {
                    transform: scale(1.1)
                }
            }
        }
    }
}